import React from "react";
import fin_compliance from "../Assets/fin-compliance.svg";
import fin_lancer from "../Assets/fin-lancer.svg";
import fin_blockchain from "../Assets/fin-blockchain.svg";

function OurPartners() {
  return (
    <div className="mt-10 pt-10 pb-10 bg-[#ecfaff]">
      <p className="text-lg sm:text-xl md:text-3xl text-[#1B8BB5] font-semibold text-center">
        Our Policies
      </p>
      <div className="flex justify-evenly flex-wrap items-center pt-10 pb-10">
        <div className="bg-white p-4 rounded-2xl mt-4">
          <img src={fin_compliance} alt="" className="w-28 sm:w-32 md:w-52" />
        </div>
        <div className="bg-white p-4 rounded-2xl mt-4">
          <img src={fin_lancer} alt="" className="w-28 sm:w-32 md:w-52" />
        </div>
        <div className="bg-white p-4 rounded-2xl mt-4">
          <img src={fin_blockchain} alt="" className="w-28 sm:w-32 md:w-52" />
        </div>
      </div>
    </div>
  );
}

export default OurPartners;
