import React from 'react'
import fintech_school from "../Assets/fintech_school.png"
import digital_asset_counsil from "../Assets/digital_asset_counsil.jpg"

function Features() {
  return (
    <div className='mt-10 mb-10'>
        <p className='text-[#1B8BB5] text-lg sm:text-2xl md:text-3xl font-semibold text-center pb-5'>Featuring Course Audits from</p>
        <div className='flex justify-evenly'>
            <img src={fintech_school} alt="" className='w-44 sm:w-72 md:w-96' />
            <img src={digital_asset_counsil} alt="" className='w-32 sm:w-52 md:w-68' />
        </div>
    </div>
  )
}

export default Features