import React from 'react'
import how_we_work from "../Assets/how_we_work.png"

function OurWork() {
  return (
    <div className='mt-20 max-sm:ml-10 max-sm:mr-10'>
        <p className='text-[#1B8BB5] text-lg sm:text-2xl md:text-3xl font-semibold text-center'>How We Work?</p>
        <div className='flex justify-evenly items-center flex-wrap'>
            <div>
                <img src={how_we_work} alt="" className='w-60 sm:w-80 md:w-96' />
            </div>
            <div className='mt-4'>
                <p className='font-semibold text-lg lg:text-xl'><li>Select a digital tool and business model of choice</li></p>
                <p className='font-semibold text-lg lg:text-xl'><li>Setup a Discovery Call for scope/recommendations</li></p>
                <p className='font-semibold text-lg lg:text-xl'><li>Sign Up/Onboarding</li></p>
            </div>
        </div>
    </div>
  )
}

export default OurWork