import React from 'react'

function TrainingProviders() {
  return (
    <div className='mt-20'>
        <p className='font-semibold text-center text-[#1B8BB5] text-lg sm:text-xl md:text-2xl'>Reach out to training providers for</p>
        <div className="grid grid-cols-6 mr-10 md:mr-20 ml-10 md:ml-20 gap-0 md:gap-10">
        {/* Product 1 */}
        <div className="col-span-6 md:col-span-3 xl:col-span-2 border shadow pt-10 pb-10 mr-6 ml-6 mt-4 cursor-default">
          <div className="flex flex-col justify-center items-center pl-4 pr-4 pt-6">
            <p className="text-lg sm:text-xl md:text-2xl text-[#1B8BB5] font-semibold ">
            Continuing Education
            </p>
            <p className="text-lg md:text-base lg:text-lg text-justify">
            Get Certifications or CE credit
            </p>
          </div>
        </div>
        {/* Product 2 */}
        <div className="col-span-6 md:col-span-3 xl:col-span-2 border shadow pt-10 pb-10 mr-6 ml-6 mt-4 cursor-default">
          <div className="flex flex-col justify-center items-center pl-4 pr-4 pt-6">
            <p className="text-lg sm:text-xl md:text-2xl text-[#1B8BB5] font-semibold ">
            Project Managers
            </p>
            <p className="text-lg md:text-base lg:text-lg text-justify">
            Hire Teacher/Trainers
            </p>
          </div>
        </div>
       
        {/* Product 3 */}
        <div className="col-span-6 md:col-span-3 xl:col-span-2 border shadow pt-10 pb-10 mr-6 ml-6 mt-4 cursor-default">
          <div className="flex flex-col justify-center items-center pl-4 pr-4 pt-6">
            <p className="text-lg sm:text-xl md:text-2xl text-[#1B8BB5] font-semibold ">
            Consultants
            </p>
            <p className="text-lg md:text-base lg:text-lg text-justify">
            Managed services
            </p>
          </div>
        </div>
        </div>

    </div>
  )
}

export default TrainingProviders;