import React from "react";
import logo from "../Assets/fin-disclosure.svg";
import header_background from "../Assets/main-header_background.png";
import app_store from "../Assets/app_store.svg";
import play_store from "../Assets/play_store.svg";
import disclosure_app from "../Assets/disclosure_app.png";

function HeaderR() {
  return (
    <div className=" h-[150vh] md:h-[100vh]">
        <img src={header_background} alt="" className="absolute h-[150vh] md:h-[100vh] w-[100%] z-[-10]" />
      <div className="pr-4 pl-4 md:pr-20 md:pl-20 flex justify-between items-center h-20 border-b border-white">
        <img src={logo} alt="" className="w-40" />
        <button className="flex items-center justify-center font-semibold text-lg md:text-xl rounded border-none pt-4 pb-4 text-[#1B8BB5] bg-white w-20 md:w-28 h-6 md:h-10">
          Sign In
        </button>
      </div>
      <div className="flex flex-wrap justify-center md:justify-between items-center pt-20 pb-20 pl-4 md:pr-20 md:pl-20">
        <div className="w-[70%] md:w-[50%]">
          <p className="text-xl md:text-3xl font-bold text-[#FFFFFF]">Disclosure App</p>
          <p className="text-lg md:text-xl w-[80%] md:w-[65%] pt-2 text-[#FFFFFF] " style={{lineHeight: "30px"}}>
            Deliver disclosures to clients, staff, solicitors, and prospects on a desktop or smartphone.
          </p>
          <div className="flex flex-wrap pt-2">
            <img src={app_store} alt="" className="w-40" />
            <img src={play_store} alt="" className="w-40" />
          </div>
        </div>
        <div className=" max-md:mt-10">
            <img src={disclosure_app} alt="" className="w-[300px] md:w-[300px] lg:w-[430px] xl:w-[550px]"  />
        </div>
      </div>
    </div>
  );
}

export default HeaderR;
