import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Figure from 'react-bootstrap/Figure';
import Trainer from '../Assets/footer_logo.svg'

const Footer = () => {
  return (
    <div className='mt-12'>
      <div className=' max-sm:pl-6 max-sm:pr-6'>
      <Container >
        <Row>
          <Col md={6} >
            <Figure >
            <Figure.Image
              src={Trainer}
              width={200}
              height={200}
            />
            </Figure>
            <p>
              Provides FINServ firms, providers, and consultants with general course audits from subject matter expert trainers in: Cryptocurrency; A.I; Digital Wallets; Web 3; DeFi; and Operations.
              We help firms to learn about new technologies, find providers, and keep clients informed. </p>
            <p>
              Our network partners support firms with Technology, Consulting, and API integrations and has worked with more than 200 SEC, State, FINRA registered firms in the U.S. and Europe since 2012.</p>
          </Col>
          <Col md={6} className='mb-10'>
            <p className='text-lg sm:text-xl md:text-3xl text-[#1B8BB5] font-semibold md:pl-10 '>Quick Links</p>
              <div className="md:pl-10 ">
                <li >Home</li>
                <li >Sign in</li>
                <li >Privacy Policy</li>
                <li >Disclaimers</li>
              </div>

          </Col>
        </Row>
      </Container>
      </div>
          <p className='bg-[#1B8BB5] text-lg m-0 text-center text-white max-sm:pl-6 max-sm:pr-6 max-sm:pt-4 max-sm:pb-4 sm:pt-2 sm:pb-2'>FIN Trainer is a division of Roberson Ventures Group, Inc. © All Rights Reserved (2018 - 2023).</p>
    </div>
  );
};

export default Footer;